.welcome {
  height: 860px;

  .container {
    height: 100%;
    position: relative;
  }
}

.welcome__imgSide {
  position: absolute;
  inset: 66px 0 0 auto;
  height: 857px;
  width: 633px;
  z-index: 1;
  object-position: left;
}

.welcome__bg {
  position: absolute;
  inset: 0;
  z-index: 0;
}

.welcome__body {
  position: relative;
  padding: 264px 0 168px;
  z-index: 2;
}

.welcome__title {
  max-width: 665px;
  margin-bottom: 44px;

  span {
    color: #018FA3;
    text-shadow: -33px -25px rgba(1,143,163,0.05);
  }
}

.welcome__text {
  max-width: 533px;
  margin-bottom: 44px;
}

.welcome__btn {
  background-color: #FAE01A;
  padding: 20px 44px;
  box-shadow: 0 6px 20px rgba(250,224,26,0.7);
  font-size: 20px;
}


@mixin welcome-992 {

  .welcome__imgSide {
    width: 510px;
  }

  .welcome__title {
    max-width: 520px;
  }

  .welcome__text {
    max-width: 495px;
  }

}

@mixin welcome-768 {

  .welcome__imgSide {
    display: none;
  }

}

@mixin welcome-576 {

  .welcome {
    height: auto;
  }

  .welcome__bg {
    display: none;
  }

  .welcome__body {
    display: flex;
    flex-direction: column;
    padding: 26vw 0 10vw;
  }

  .welcome__title,
  .welcome__text {
    text-align: center;
    max-width: none;
    margin-bottom: 6vw;
  }

  .welcome__title {

    span {
      text-shadow: none;
    }
  }

  .welcome__btn {
    align-self: center;
    font-size: calc(var(--indDesc) * 1.5);
    padding: calc(var(--indDesc) * 1.5) calc(var(--indDesc) * 3);
  }

}