.contacts {
  padding: 125px 0 87px;
}

.contacts__body {
  position: relative;
}

.contacts__img {
  position: absolute;
  right: -90px;
  top: -125px;
}

.contacts__info {
  border-radius: 30px;
  background: #018FA3;
  color: #fff;
  width: 590px;
  padding: 12px 41px 38px;
  margin-bottom: 57px;
  position: relative;

  &::before {
    content: '';
    display: block;
    position: absolute;
    height: 200px;
    width: 200px;
    border-radius: 50%;
    background-color: #95D8E0;
    left: 50px;
    top: -50px;
    z-index: -1;
  }
}

.contacts__title {
  font-weight: 500;
  margin-bottom: 20px;
  text-align: center;
}

.contacts__list {

  dt {
    font-weight: 600;
    margin-bottom: 8px;
  }

  dd {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    transition: opacity 0.3s;

    &:hover,
    &:focus-visible {
      opacity: 0.5;
    }
  }
}

.contacts__map {
  height: 539px;
  width: 794px;
  margin: 0 auto;
  position: relative;

  &::before {
    content: '';
    display: block;
    position: absolute;
    height: 175px;
    width: 175px;
    border-radius: 50%;
    border: 25px solid #FAE01A;
    background-color: #FFF7BE;
    left: -65px;
    top: 50px;
    z-index: -1;
  }

  &::after {
    content: '';
    display: block;
    position: absolute;
    height: 175px;
    width: 175px;
    border-radius: 50%;
    border: 25px solid #95D8E0;
    background-color: #018FA3;
    right: -65px;
    bottom: 50px;
    z-index: -1;
  }
}

.contacts__mapImg {
  border-radius: 20px;
  overflow: hidden;
}


@mixin contacts-1400 {

  .contacts__img {
    display: none;
  }

  .contacts__info {
    margin-left: auto;
    margin-right: auto;
  }

}

@mixin contacts-992 {

  .contacts {
    padding-top: 60px;
  }

  .contacts__info {

    &::before {
      display: none;
    }
  }

  .contacts__map {
    height: auto;
    max-width: 794px;
    width: 100%;

    &::before,
    &::after {
      display: none;
    }
  }

}


@mixin contacts-768 {

  .contacts__info {
    max-width: 590px;
    width: 100%;
  }

}


@mixin contacts-576 {

  .contacts {
    padding: 10vw 0 10vw;
  }

  .contacts__info {
    padding: 2.1vw 7.1vw 6.6vw;
    margin-bottom: 9.5vw;
  }

}