.certificate__container {
  max-width: 1900px;
  margin: 0 auto;
  padding-bottom: 125px;
  overflow: hidden;
}

.certificate__body {
  position: relative;

  &::before {
    content: url(/assets/src/img/certificate/decoration/decor.png);
    display: block;
    position: absolute;
    left: -340px;
    top: 0;
    height: 440px;
    width: 536px;
  }

  &::after {
    content: '';
    display: block;
    position: absolute;
    height: 136px;
    width: 136px;
    border: 10px solid #FFF7BE;
    border-radius: 50%;
    right: -42px;
    bottom: -42px;
  }
}

.certificate__title {
  margin-bottom: 60px;
  text-align: center;
  position: relative;
}

.certificate__items {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  position: relative;
  z-index: 1;
}

.certificate__item {
  border: 1px solid rgba(0,0,0,0.1);
  height: 200px;
  width: 285px;
}


@mixin certificate-576 {

  .certificate__container {
    padding-bottom: 60px;
  }

  .certificate__body {

    &::before,
    &::after {
      display: none;
    }
  }

  .certificate__title {
    margin-bottom: 10vw;
  }

  .certificate__item {
    height: auto;
    max-width: 285px;
    width: auto;
  }

}