@import "../fonts.scss";

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: Commissioner, Arial, sans-serif;
  color: #000;

  *,
  *::before,
  *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  a,
  input,
  textarea {
    color: inherit;
    font: inherit;
  }

  button {
    cursor: pointer;
  }

  h1, h2, h3, h4, h5, h6 {
    font-weight: inherit;
  }

  a:not(.outline),
  input:not(.outline),
  textarea:not(.outline),
  button:not(.outline) {
    outline: none;
  }

  a:not(.textDecoration) {
    text-decoration: none;
  }

  ul:not(.listStyle) {
    list-style: none;
  }

  img {
    object-fit: cover;
    max-width: 100%;
  }
}

.container {
  margin: 0 auto;
  max-width: 1240px;
  padding: 0 20px;
}

.container--outer {
  margin: 0 auto;
  max-width: 1440px;
}

.myMain {
  flex-grow: 1;
}