.blockInput--1 {
  background: #f5f5f5;
  box-shadow: inset 0 2px 0 #ebebeb;
  border: none;
  border-radius: 100px;
  font-family: MuseoSansCyrl, sans-serif;
  font-weight: 300;
  font-size: 1.6rem;
  letter-spacing: 0.1em;
  line-height: lh(16,19);
  max-width: 370px;
  padding: 31px 20px 30px;
  text-align: center;
  width: 100%;

  @include inputPlaceholder(16px, rgba(160,160,160,0.7), 0.7);
}