.request {
  position: fixed;
  inset: 0;
  z-index: var(--ziModals);
  background-color: rgba(0,0,0,0.2);
}

.request__body {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  max-width: 400px;
  border-radius: 30px;
  background-color: #F6FAFF;
  padding: 20px 32px 32px;
}

.request__title {
  font-weight: 500;
  margin-bottom: 32px;
  text-align: center;

  span {
    color: #018FA3;
  }
}

.request__form {

  button {
    padding: 19px 44px 21px;
    background-color: #FAE01A;
    display: block;
    margin: 0 auto;
    font-size: 20px;
  }
}

.request__formFields {
  display: flex;
  flex-direction: column;
  row-gap: 12px;
  margin-bottom: 32px;

  input,
  textarea {
    padding: 19px 20px;
    font-size: 18px;
    font-weight: 300;
    border-radius: 15px;
    border: none;
    background-color: #fff;

    @include inputPlaceholder(18px,#C2C2C2,1);
  }

  textarea {
    height: 120px;
    resize: none;
  }
}