.header {
  font-size: 16px;
  position: absolute;
  width: 100%;
  z-index: var(--ziHeader);
}

.header__body {
  display: flex;
  padding: 20px 0;
}

.header__logo {
  flex-shrink: 0;
  margin-right: calc(var(--indBox) * 5.1);
}

.header__menu {
  flex-grow: 1;
  margin-top: 20px;

  ul {
    align-items: center;
    display: flex;
    gap: 40px;
  }

  a {
    color: inherit;
    font-weight: 500;
    transition: opacity 0.3s;

    &:hover,
    &:focus-visible {
      opacity: 0.5;
    }
  }
}

.header__phone {
  display: flex;
  align-items: center;
  align-self: flex-start;
  margin-top: 10px;
  font-size: 1.13em;
  font-weight: 600;
  
  &:hover,
  &:focus-visible {
    
    div {
      background-color: #FAE01A;
    }
  }

  div {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    background-color: #018FA3;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin-right: 8px;
    transition: background-color 0.3s;
  }

  svg {
    fill: #fff;
    height: 16px;
    width: 16px;
  }

}


@mixin header-992 {

  .header__body {
    align-items: center;
    justify-content: space-between;
  }

  .header__logo {
    margin-right: 0;
  }

  .header__menu {
    display: none;
  }

  .header__phone {
    margin-top: 0;
    align-self: auto;
  }

}


@mixin header-576 {

  .header {
    font-size: 14px;
  }

  .header__body {
    padding: 10px 0;
  }

  .header__logo {
    height: 15.2vw;
    width: 17.7vw;
  }

  .header__phone {

    div {
      height: 6.9vw;
      width: 6.9vw;
    }

    svg {
      height: 2.7vw;
      width: 2.7vw;
    }
  }

  .header__burgerBtn {
    height: 20px;
    width: 20px;
  }

}
