.footer {
  background-color: #0A2530;
}

.footer__container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 56px 0;
  overflow: hidden;
}

.footer__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: -30px -50px;
}

.footer__logo {
  margin: 15px 50px;
  flex-shrink: 0;
}

.footer__menu {
  font-weight: 500;
  flex-grow: 1;
  color: #fff;
  margin: 15px 50px;

  ul {
    display: flex;
    gap: 40px;
  }

  a {
    transition: opacity 0.3s;

    &:hover,
    &:focus-visible {
      opacity: 0.5;
    }
  }
}

.footer__banner {
  width: 187px;
  height: 51px;
  margin: 15px 50px;
}


@mixin footer-768 {

  .footer__body {
    align-items: flex-start;
    margin: -15px 0;
  }

  .footer__logo {
    margin: 15px 0;
  }

  .footer__menu {
    margin: 15px 0;
    flex-grow: 0;

    ul {
      flex-direction: column;
      align-items: center;
    }
  }

  .footer__banner {
    margin: 15px 0;
  }

}


@mixin footer-576 {

  .footer__body {
    flex-direction: column;
    align-items: center;
  }

}