.thank {
  position: fixed;
  inset: 0;
  z-index: var(--ziModals);
  background-color: rgba(0,0,0,0.2);
}

.thank__body {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  max-width: 400px;
  width: 100%;
  border-radius: 30px;
  background-color: #F6FAFF;
  padding: 80px 37px 103px 27px;
}

.thank__img {
  height: 120px;
  width: 120px;
  margin: 0 auto;
  margin-bottom: 16px;
}

.thank__text {
  font-size: 36px;
  font-weight: 500;
  text-align: center;

  span {
    color: #018FA3;
  }

  p {
    margin-bottom: 36px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}


@mixin thank-576 {

  .thank__body {
    padding: 40px 20px 50px;
  }

  .thank__img {
    height: 80px;
    width: 80px;
  }

  .thank__text {
    font-size: 29px;

    p {
      margin-bottom: 20px;
    }
  }

}