.blockBtn {
  align-items: center;
  border-radius: 40px;
  border: none;
  display: inline-flex;
  font-weight: 500;
  justify-content: center;
  text-align: center;
  transition: transform 0.3s;

  &:hover,
  &:focus-visible {
    transform: scale(1.1);
  }
}


