.feedback {
  margin-bottom: 80px;

  .slick-track {
    display: flex;
  }

  .slick-slide {
    height: auto;

    & > div {
      height: 100%;
    }
  }

  .slick-arrow {

    &::before,
    &::after {
      content: '';
      display: block;
      height: 60px;
      width: 24px;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
    }
  }

  .slick-prev {
    left: -75px;

    &::before {
      background-image: url(/assets/src/img/common/row-left.svg);
    }
  }

  .slick-next {
    right: -68px;

    &::before {
      background-image: url(/assets/src/img/common/row-right.svg);
    }
  }

  .slick-prev,
  .slick-next {
    top: 44%;
  }
}

.feedback__title {
  text-align: center;
  margin-bottom: 60px;
}

.feedback__slider {
  margin: 0 -9px;
}

.feedback__sliderItemWrapper {
  height: 100%;
}

.feedback__sliderItem {
  border-radius: 30px;
  border: 4px solid #018FA3;
  height: 100%;
  padding: 36px 24px 36px 33px;
  margin: 0 9px;
}

.feedback__sliderItemHeader {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.feedback__sliderItemImg {
  flex-shrink: 0;
  background-color: #C4C4C4;
  border-radius: 50%;
  margin-right: 20px;
  height: 80px;
  width: 80px;
  overflow: hidden;
}

.feedback__sliderItemTitle {
  font-weight: 600;
}

.feedback__sliderItemText {
  font-size: 20px;
}


@mixin feedback-1400 {

  .feedback__slider {
    margin: 0 auto;
    width: 812px;
  }

}

@mixin feedback-992 {

  .feedback__slider {
    width: 410px;
  }

}

@mixin feedback-576 {

  .feedback {

    .slick-arrow {
      top: 47%;

      &::before,
      &::after {
        height: 40px;
        width: 15px;
      }
    }

    .slick-prev {
      left: -10px;
    }

    .slick-next {
      right: -17px;
    }
  }

  .feedback__title {
    margin-bottom: 10vw;
  }

  .feedback__slider {
    width: 100%;
    max-width: 410px;
  }

  .feedback__sliderItem {
    padding: 6.2vw 4.1vw 6.2vw 5.7vw;
  }

  .feedback__sliderItemImg {
    margin-right: 3.4vw;
    height: 13.9vw;
    width: 13.9vw;
  }

  .feedback__sliderItemText {
    font-size: calc(var(--indDesc) * 1.4);
  }

}


