.catalog {
  background-color: #F6FAFF;
  padding: 70px 0 80px;
  margin-bottom: 60px;

  .slick-list {
    z-index: 1;
  }

  .slick-slide {
    margin: 0 10px;
  }

  .slick-arrow {

    &::before,
    &::after {
      content: '';
      display: block;
      height: 60px;
      width: 24px;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
    }
  }

  .slick-prev {
    left: -75px;

    &::before {
      background-image: url(/assets/src/img/common/row-left.svg);
    }
  }

  .slick-next {
    right: -68px;

    &::before {
      background-image: url(/assets/src/img/common/row-right.svg);
    }
  }

  .slick-prev,
  .slick-next {
    top: 47%;
  }

  .colorScheme1 {

    .catalog__sliderItemTitle,
    .catalog__sliderItemSelectBtn {
      background-color: #018FA3;
    }
  }

  .colorScheme2 {

    .catalog__sliderItemTitle,
    .catalog__sliderItemSelectBtn {
      background-color: #955EEA;
    }
  }

  .colorScheme3 {

    .catalog__sliderItemTitle,
    .catalog__sliderItemSelectBtn {
      background-color: #4ECA30;
    }
  }

  .colorScheme4 {

    .catalog__sliderItemTitle,
    .catalog__sliderItemSelectBtn {
      background-color: #FFE41B;
    }
  }

  .colorScheme5 {

    .catalog__sliderItemTitle,
    .catalog__sliderItemSelectBtn {
      background-color: #FF4D94;
    }
  }

  .colorScheme6 {

    .catalog__sliderItemTitle,
    .catalog__sliderItemSelectBtn {
      background-color: #FFA724;
    }

    .catalog__sliderItemCount {
      margin: 24px 0 39px;
    }
  }
}

.catalog__title {
  text-align: center;
  margin-bottom: 70px;
}

.catalog__slider {
  height: 778px;
  position: relative;
  margin: 0 auto;

  &::before {
    content: '';
    display: block;
    position: absolute;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background-color: #FFF7BE;
    left: -65px;
    top: 25px;
  }

  &::after {
    content: '';
    display: block;
    position: absolute;
    width: 252px;
    height: 252px;
    border-radius: 50%;
    border: 16px solid #089EA5;
    right: 68px;
    top: -62px;
  }
}

.catalog__sliderItem {
  display: flex;
  flex-direction: column;
  border-radius: 30px;
  background: #fff;
  box-shadow: 0 52px 34px -27px rgba(225, 225, 225, 0.15);
  text-align: center;
  overflow: hidden;
  height: 778px;

  &.openList {
    height: auto;
  }
}

.catalog__sliderItemTitle {
  padding: 26px 0;
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
}

.catalog__sliderItemBody {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding: 60px 0;
}

.catalog__sliderItemPrice {

  & + .catalog__sliderItemCount {
    margin: 48px 0 73px;
  }
}

.catalog__sliderItemAge {
  margin: 24px 0 8px;
}

.catalog__sliderItemCount {
  margin-bottom: 60px;
}

.catalog__sliderItemContent {
  margin-bottom: 30px;

  li {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.catalog__sliderItemMoreBtn {
  border: none;
  background-color: transparent;
  color: #018FA3;

  &:focus {
    opacity: 0;
  }
}

.catalog__sliderItemSelectBtn {
  border: none;
  border-radius: 40px;
  background-color: grey;
  color: #fff;
  align-self: center;
  font-weight: 500;
  padding: 19px 60px;
  margin-top: auto;
  transition: transform 0.3s;

  &:hover,
  &:focus-visible {
    transform: scale(1.1);
  }
}


@mixin catalog-1400 {

  .catalog__slider {
    width: 80vw;
  }

}


@mixin catalog-992 {

  .catalog {

    .colorScheme6 {

      .catalog__sliderItemCount {
        margin: 9px 0 25px;
      }
    }
  }

  .catalog__slider {
    width: 80vw;
    height: 610px;
  }

  .catalog__sliderItemBody {
    padding: 30px 0;
  }

  .catalog__sliderItem {
    height: 610px;
  }

  .catalog__sliderItemPrice {

    & + .catalog__sliderItemCount {
      margin: 30px 0 55px;
    }
  }

  .catalog__sliderItemCount {
    margin-bottom: 30px;
  }
}


@mixin catalog-768 {

  .catalog__slider {
    max-width: 400px;
    height: 550px;
  }

  .catalog__sliderItem {
    height: 550px;
  }

  .catalog__sliderItemPrice {

    & + .catalog__sliderItemCount {
      margin-bottom: 30px;
    }
  }

}


@mixin catalog-576 {

  .catalog {

    .colorScheme6 {

      .catalog__sliderItemCount {
        margin-bottom: 4vw;
      }
    }

    .slick-arrow {

      &::before,
      &::after {
        height: 40px;
        width: 15px;
      }
    }

    .slick-prev {
      left: -10px;
    }

    .slick-next {
      right: -17px;
    }
  }

  .catalog__title {
    margin-bottom: 12vw;
  }

  .catalog__slider {
    height: calc( var(--indDesc) * 40 );
    width: 100%;
    max-width: 320px;

    &::after,
    &::before {
      display: none;
    }
  }

  .catalog__sliderItem {
    height: calc( var(--indDesc) * 40 );
  }

  .catalog__sliderItemBody {
    padding: 5vw 0;
  }

  .catalog__sliderItemAge {
    margin-top: 4vw;
  }

  .catalog__sliderItemCount {
    margin-bottom: 5vw;
  }

  .catalog__sliderItemPrice {

    & + .catalog__sliderItemCount {
      margin-bottom: 5vw;
    }
  }

  .catalog__sliderItemContent {
    margin-bottom: 5vw;

    li {
      margin-bottom: 4vw;
    }
  }

}

