.about__container {
  max-width: 1264px;
  margin: 0 auto;
  padding: 130px 0 60px;
  overflow: hidden;
}

.about__items {
  margin-bottom: 140px;
}

.about__item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap-reverse;
  margin-bottom: 115px;
  gap: 120px 0;

  &:nth-child(2n) {
    flex-wrap: wrap;

    .about__itemText {
      order: 1;
      margin-left: 122px;
      margin-right: 130px;

      img {
        inset: auto -100px -42px auto;
      }
    }

    .about__itemPhotoDecor {
      left: -32px;
      right: auto;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.about__itemText {
  max-width: 460px;
  position: relative;
  margin-left: 130px;
  margin-right: 122px;

  &::before {
    content: '';
    border-radius: 30px;
    background-color: #FFF7BE;
    display: block;
    position: absolute;
    top: -25px;
    right: -30px;
    height: 100%;
    width: 95%;
    z-index: 1;
  }

  img {
    height: 200px;
    width: 200px;
    position: absolute;
    z-index: 0;
    top: -80px;
    left: -50px;
  }

  p {
    position: relative;
    z-index: 2;
  }
}

.about__itemPhoto {
  height: 343px;
  width: 488px;
  position: relative;
}

.about__itemPhotoDecor {
  position: absolute;
  right: -32px;
  bottom: -32px;
}

.about__itemPhotoImg {
  position: relative;
}

.about__roulette {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 77px;
  margin: 0 auto;
  height: 720px;
  width: 1000px;
  position: relative;
}

.about__rouletteImg {
  background-color: #FAE01A;
  border-radius: 50%;
  height: 322px;
  width: 322px;
  margin-bottom: 16px;

  img {
    border-radius: 50%;
    position: relative;
    top: 7px;
    left: 19px;
    height: 300px;
    width: 300px;
  }
}

.about__rouletteText {
  max-width: 375px;
  text-align: center;
}

.about__rouletteItems {
  position: absolute;
  inset: 0;
}

.about__rouletteItem {
  position: absolute;

  &:nth-child(1) {
    top: 0;
    left: 101px;
  }

  &:nth-child(2) {
    top: 208px;
    left: 0;
  }

  &:nth-child(3) {
    top: 416px;
    left: 101px;
  }

  &:nth-child(4) {
    top: 508px;
    left: 424px;
  }

  &:nth-child(5) {
    top: 416px;
    left: 759px;
  }

  &:nth-child(6) {
    top: 208px;
    left: 862px;
  }

  &:nth-child(7) {
    top: 0;
    left: 742px;
  }
}

.about__rouletteItemImg {
  height: 120px;
  width: 120px;
  margin: 0 auto 4px;
}

.about__rouletteItemText {
  text-align: center;
}


@mixin about-992 {

  .about__roulette {
    height: auto;
    width: auto;
    padding-top: 0;
  }

  .about__rouletteText {
    margin-bottom: 60px;
  }

  .about__rouletteItems {
    display: flex;
    flex-direction: column;
    position: relative;
    row-gap: 30px;
  }

  .about__rouletteItem {

    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5),
    &:nth-child(6),
    &:nth-child(7) {
      inset: auto;
      position: relative;
    }
  }

}


@mixin about-768 {

  .about__container {
    padding: 70px 0 40px;
    overflow: visible;
  }

  .about__items {
    max-width: 488px;
    margin: 0 auto;
  }

  .about__item {
    margin-bottom: 60px;
    row-gap: 60px;

    &:nth-child(2n) {

      .about__itemText {
        margin-left: 0;
        margin-right: 0;
      }

      .about__itemPhotoDecor {
        left: 0;
        right: 0;
      }
    }
  }

  .about__itemText {
    margin-left: 0;
    margin-right: 0;
    padding: 10px 15px;
    max-width: none;

    &::before {
      top: 0;
      right: 0;
      width: 100%;
    }

    img {
      display: none;
    }
  }

  .about__itemPhoto {
    height: auto;
    width: 100%;
  }

  .about__itemPhotoDecor {
    display: none;
  }

  .about__roulette {
    padding-top: 100px;
  }

}


@mixin about-576 {

  .about__container {
    padding: 40px 0;
  }

  .about__item {
    row-gap: 10vw;
    margin-bottom: 13vw;
  }

  .about__roulette {
    padding-top: 17vw;
  }

  .about__rouletteImg {
    height: 56vw;
    width: 56vw;

    img {
      height: 52vw;
      width: 52vw;
    }
  }
  
  .about__rouletteText {
    margin-bottom: 10vw;
  }

  .about__rouletteItemImg {
    height: 21vw;
    width: 21vw;
  }

}